import {ReleaseDateButton} from "./ReleaseDateButton";
import {
  DateIcon,
  DateShow,
  ExactButton,
  FilterOption,
  FilterOptionArrow,
  FilterOptionWrapper
} from "../../Filter.styles";
import React, {useState} from "react";
import {filterDate} from "../../../../store/modules/Filter/slice";
import {useDispatch} from "react-redux";


export const ReleaseDate = () => {
  const [dateCollapse, setDateCollapse] = useState(false)
  const releaseDates = [
    {title: ' 2025 ', date: 2025},
    {title: ' 2015 ', date: 2015},
    {title: ' 2010 ', date: 2010},
    {title: ' 2000 ', date: 2000},
    {title: ' 1990 ', date: 1990},
    {title: ' 1980 ', date: 1980},
    {title: ' 1970 ', date: 1970},
    {title: ' 1960 ', date: 1960},
  ]
  const dispatch = useDispatch()
  const handleShowDate = () => {
    setDateCollapse(!dateCollapse)
  }
  const handleSetDefault = () => {
    dispatch(filterDate(2025))
  }

  return (
    <FilterOptionWrapper>
      <FilterOption onClick={handleShowDate}>
        <DateIcon/>
        
        <FilterOptionArrow datecollapse={dateCollapse ? 1 : 0}></FilterOptionArrow>
      </FilterOption>
      <div>
        {dateCollapse &&
        <DateShow>
          {releaseDates.map((item) =>
            <ReleaseDateButton
              key={item.date + item.title}
              date={item.date}
              title={item.title}
            >
            </ReleaseDateButton>
          )}
          <ExactButton onClick={handleSetDefault}> </ExactButton>
        </DateShow>
        }
      </div>
    </FilterOptionWrapper>
  );
};

